* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #dadada;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.country-list {
  will-change: opacity;
  padding: 0;
  list-style: none;
  transition: opacity .5s;
}

.country__item {
  flex-direction: row;
  align-items: center;
  display: flex;
}

.country__wrapper {
  height: 50px;
  min-width: 90px;
  justify-content: center;
  align-items: center;
  margin: 10px 15px 10px 0;
  display: flex;
}

.country__image {
  max-height: 50px;
  margin: auto;
}

.country__name {
  font-size: 22px;
}

.country-info {
  max-width: 400px;
}

.info__image {
  max-width: 500px;
}

.show {
  animation: showing .5s;
}

@keyframes showing {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

